<template>
	<view>
		<topicInfo :info="info"></topicInfo>
		<divider></divider>
		
		<view class="p-2 flex align-center border-bottom" 
		hover-class="bg-light" v-for="(item,index) in hotList" :key="index">
			<text class="iconfont icon-xuanze text-main"></text>
			<text class="font text-dark text-ellipsis">
				{{item.title}}
			</text>
		</view>
		
		<divider></divider>
		
		<!--tab-->
		<view class="flex align-center py-2">
			<view class="flex-1 flex align-center justify-center" 
			v-for="(item,index) in tabBars" :key="index" :class="index === tabIndex ? 'font-lg font-weight-bold  text-main' : 'font-md'"
			@click="changeTab(index)">{{item.name}}</view>
			
		</view>
		
		<!--列表-->
		<template v-if="listData.length > 0">
			<block v-for="(item,index) in listData" :key="index">
				<commonList :item="item" :index="index"></commonList>
				<divider></divider>
			</block>
			<!--上拉加载-->
			<loadMore :loadmore="loadtext"></loadMore>
		</template>
		<template v-else>
			<noThing></noThing>
		</template>
		
		
	</view>
</template>

<script>
	const demo = [
		{
			username: '昵称',
			userpic: '../../static/default.jpg',
			newstime: '2019-10-20 下午04:30',
			isFollow: false,
			title: '我是标题',
			titlepic: '../../static/demo/datapic/11.jpg',
			support: {
				type: '',
				support_count: 0,
				unsupport_count: 0
			},
			comment_count: 2,
			share_num: 2
		},
		{
			username: '昵称',
			userpic: '../../static/default.jpg',
			newstime: '2019-10-20 下午04:30',
			isFollow: false,
			title: '我是标题',
			titlepic: '../../static/demo/datapic/11.jpg',
			support: {
				type: '',
				support_count: 0,
				unsupport_count: 0
			},
			comment_count: 2,
			share_num: 2
		},
		{
			username: '昵称',
			userpic: '../../static/default.jpg',
			newstime: '2019-10-20 下午04:30',
			isFollow: false,
			title: '我是标题',
			titlepic: '../../static/demo/datapic/11.jpg',
			support: {
				type: '',
				support_count: 0,
				unsupport_count: 0
			},
			comment_count: 2,
			share_num: 2
		}
	];
	
	import topicInfo from '../../components/topic-detail/topic-info.vue';
	import commonList from '../../components/common/common_list.vue';
	import noThing from '../../components/common/no-thing.vue';
	import loadMore from '../../components/common/load_more.vue';
	export default {
		components:{
			topicInfo,
			commonList,
			noThing,
			loadMore
		},
		data() {
			return {
				info : {
					cover: "/static/bgimg/1.jpg",
					desc: "话题描述",
					news_count: 10,
					title: "话题名称",
					today_count: 0
				},
				hotList : [{
					title : " 【新人必读】uni-app"
				},{
					title : " 【新人必读】uni-app2"
				}],
				tabIndex : 0,
				tabBars : [{
					name : "默认"
				},{
					name : "最新"
				}],
				// 默认
				list1 : [],
				loadtext1 : "上拉加载更多",
				// 最新
				list2 : [],
				loadtext2 : "上拉加载更多",
			}
		},
		computed:{
			listData(){
				if(this.tabIndex === 0){
					return this.list1
				}else{
					return this.list2
				}
			},
			loadtext(){
				if(this.tabIndex === 0){
					return this.loadtext1
				}else{
					return this.loadtext2
				}
			}
		},
		onLoad(e) {
			if(e.detail){
				let res = JSON.parse(e.detail)
				console.log(res)
			}
			this.list1 = demo
		},
		// 滚动到底部
		onReachBottom(){
			this.loadmore()
		},
		methods: {
			changeTab(index){
				this.tabIndex = index
			},
			// 上拉加载更多
			loadmore(){
				// 拿到当前列表
				let index = this.tabIndex
				console.log(this.loadtext)
				// 判断是否处于可加载状态
				if(this.loadtext !== '上拉加载更多'){
					return ;
				}
				// 设置上来加载状态处于加载中
				if(index === 0){
					this.loadtext1 = '加载中'
				}else{
					this.loadtext2 = '加载中'
				}
				// 请求数据
				setTimeout(() => {
					this['list'+(index+1)] = [...this['list'+(index+1)],...this['list'+(index+1)]]
					this['loadtext'+(index+1)] = '加载中...'
				},1000)
			}
		}
	}
</script>

